import { Injectable } from '@angular/core';

import { Page } from '../classes/page';

@Injectable()
export class PagerService {

  constructor() { }

  fillPages(objects: any[], qtdPerPage: number, qtdObjects: number){
    let count = 0;
    let countPages = 0;
    let page;
    let i = 0;
    let pages = [];
    let objs = [];
    if (qtdObjects > -1) {
      let index = 0;
      for (let obj of objects) {
        if (index < qtdObjects) {
          objs.push(obj);
        }
        index++;
      }
    } else {
      objs = objects;
    }
    for(let object of objs){
      i++;
      if(count == 0){
        countPages++;
        page = new Page();
        page.pageNumber = countPages;
        page.content = [];
      }
      page.content.push(object);
      if(page.content.length == qtdPerPage){
        pages.push(page);
        count = 0;
      }else {
        count++;
        if(i == objs.length){
          pages.push(page);
        }
      }
    }
    return pages;
  }

}
