import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { environment } from '../../../../environments/environment';
import { NewCategory } from '../classes/new-category';

@Injectable({
  providedIn: 'root'
})
export class NewCategoryService {

  private apiURL: string = `${environment.apiURL}/news-category`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json'} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota não encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token inválido ou expirado';
    } else if (error.status === 400) {
      message = `Os dados enviados são inválidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`NewCategoryService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('NewCategoryService: ' + mensagem);
  }

  getCategory (keyCategory: string): Observable<any> {
    return this.http.get(`${this.apiURL}/${keyCategory}/active`, { headers: this.options }).pipe(
      tap(_ => this.log(`fetched new category with id=${keyCategory}`)),
      catchError(this.handleError<NewCategory>('getCategory'))
    );
  }

  getCategories (): Observable<any[]> {
    return this.http.get<any[]>(`${this.apiURL}/active`, { headers: this.options }).pipe(
      tap(newCategories => this.log(`fetched new categories`)),
      catchError(this.handleError('getCategories', []))
    );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}