import { Component, OnInit, Input } from '@angular/core';

import { NewCategory } from '../../classes/new-category';
import { NewCategoryService } from '../../angular-services/new-category.service';
import { New } from '../../classes/new';
import { NewService } from '../../angular-services/new.service';
import { Page } from '../../classes/page';
import { PagerService } from '../../angular-services/pager.service';

@Component({
  selector: 'app-noticias',
  templateUrl: './noticias.component.html',
  styleUrls: ['./noticias.component.css']
})
export class NoticiasComponent implements OnInit {

  @Input('amountNews') amountNews: number;
  @Input('amountPerPage') amountPerPage: number;
  showPages: boolean = true;
  news: New[] = [];
  newsPerCategory: New[] = [];
  categories: NewCategory[] = [];
  categorySelected: NewCategory;
  pageSelected: Page;
  pages: Page[] = [];

  constructor(private newService: NewService, private newCategoryService: NewCategoryService, private pagerService: PagerService) { }

  ngOnInit() {
    this.getNews();
    this.getCategories();
  }

  verifyPages() {
    if ((this.amountNews - this.amountPerPage) > 0) {
      this.showPages = true;
    } else {
      this.showPages = false;
    }
  }

  getNews() {
    this.newService.getNews().subscribe(
      response => {
        console.log(response);
        this.news = response;
        this.newsPerCategory = response;
        this.amountNews = this.news.length;
        this.pages = this.pagerService.fillPages(response, this.amountPerPage, this.amountNews);
        this.pageSelected = this.pages[0];
        this.verifyPages();
      }
    );
  }

  getCategories() {
    this.newCategoryService.getCategories().subscribe(
      response => {
        this.categories = response;
      }
    );
  }

  selectPage(page: Page){
    this.pageSelected = page;
  }

  selectAll() {
    if(this.categorySelected != null) {
      this.categorySelected = null;
      this.newsPerCategory = this.news;
      this.pages = this.pagerService.fillPages(this.newsPerCategory, this.amountPerPage, this.amountNews);
      this.pageSelected = this.pages[0];
      this.verifyPages();
    }
  }

  selectCategory(category){
    if(category != this.categorySelected) {
      this.categorySelected = category;
      this.newsPerCategory = [];
      for (let news of this.news) {
        if (news.category._id == this.categorySelected._id) {
          this.newsPerCategory.push(news);
        }
      }
      this.pages = this.pagerService.fillPages(this.newsPerCategory, this.amountPerPage, this.amountNews);
      this.pageSelected = this.pages[0];
      this.verifyPages();
    }
  }
}
